<template>
  <div style="white-space: pre;">
      <p>{{ fileContent }}</p>
  </div>
</template>

  
<script>
  export default {
      name: 'FileList',

      data() {
          return {
              fileContent: ''
          };
      },
      mounted() {
          this.fetchFileContent();
      },
      methods: {
          async fetchFileContent() {
              try {
                  const response = await fetch('/data/resources/flist');
                  this.fileContent = await response.text();
              } catch (error) {
                  console.error('Error fetching file content:', error);
              }
          }
      }

  };
</script>
